.button {
  all: unset;
  background-color: var(--primary);
  box-sizing: border-box;
  border-radius: 60px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 18px 36px;
  display: inline-flex;
  position: relative;
  overflow: hidden;
}

.button .text-wrapper {
  color: var(--white);
  font-family: var(--desktop-label-large-font-family);
  font-size: var(--desktop-label-large-font-size);
  font-style: var(--desktop-label-large-font-style);
  font-weight: var(--desktop-label-large-font-weight);
  letter-spacing: var(--desktop-label-large-letter-spacing);
  line-height: var(--desktop-label-large-line-height);
  text-align: center;
  white-space: nowrap;
  width: -moz-fit-content;
  width: fit-content;
  margin-top: -1px;
  position: relative;
}

.button:hover {
  background-color: var(--white);
  border: 1px solid;
  border-color: var(--primary);
}

.button:hover .text-wrapper {
  color: var(--primary);
}

@media only screen and (width <= 390px) {
  .button {
    padding: 10px 20px;
  }

  .button .text-wrapper {
    letter-spacing: 0;
    margin-top: -1px;
    font-family: Montserrat, Helvetica;
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
  }
}

